@import url("https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Cinzel Decorative", cursive;
  font-size: 2rem;
  height: 100vh;
  color: #222;
  background-color: #eabfb9;
  display: flex;
  align-items: center;
  justify-content: center;
    @media only screen and (max-width: 480px) {  
    height: auto;
    width: auto;
    
  }
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 112rem;
  gap: 4rem;
  padding: 6rem 0;
  border: 2px solid #222;
  outline: 2px solid #222;
  outline-offset: 1.2rem;
 @media only screen and (max-width: 480px) {  
  margin-bottom: auto;

  }
}




h1 {
  font-weight: 400;
  font-size: 6rem;
  letter-spacing: 2px;
  word-spacing: 5px;
    @media only screen and (max-width: 480px) {  
  font-size: 3rem;
    
  }
}

h2 {
  text-align: center;
  margin-bottom: 2.4rem;
    @media only screen and (max-width: 480px) {  
   font-size: 2rem;
    
  }
}

.weatherWrapper{
   @media only screen and (max-width: 480px) {  
    width: 30%;
  }
}
input {
  font-family: inherit;
  color: inherit;
  font-size: 2rem;
  background-color: #f0d2ce;
  padding: 1.6rem 3.2rem;
  border: none;
  width: 32rem;
  border-radius: 10px;
}

input:focus {
  outline: 2px solid #222;
}

.weather {
  list-style: none;
  display: flex;
  gap: 2.4rem;

   @media only screen and (max-width: 480px) {  
    width: auto;
    padding: auto;
    flex-direction: column;
    align-items: center;
    
  }
}

.day {
  background-color: #f0d2ce;
  padding: 1.6rem 0 2rem;
  width: 12.8rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.2rem;
  cursor: pointer;
  @media only screen and (max-width: 480px) {  
  width: 90%;    
  }
}

.day span {
  font-size: 5.2rem;
}

.day.important {
  background-color: #ecc5c0;
  outline: 2px solid #222;
}

.loader {
  font-size: 2.4rem;
  font-weight: 700;
}

.err{
  font-size: 1.7rem;
}